/*About*/

.about-item {
  border: 1px solid green;
  border-radius: 5px;
  transition: all 0.4s ease-in-out;
  box-shadow: 1px 2px 15px rgba(0, 0, 0, 0.1);
}
.about-item:hover {
  cursor: default;
  transform: translateY(-5px);
  border: 1px solid gold;
  box-shadow: 1px 4px 15px rgba(0, 0, 0, 0.32);
}
.abt-text {
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
}
.large-text {
  font-size: 3rem;
  font-weight: 700;
  color: green;
}
.small-text {
  padding-left: 3rem;
  position: relative;
  text-transform: uppercase;
  font-size: 1.2rem;
  color: var(--color-grey-1);
  letter-spacing: 2px;
}
.small-text::before {
  content: "";
  position: absolute;
  left: 0;
  top: 15px;
  width: 2rem;
  height: 2px;
  background-color: grey;
}
