.open-menu-button {
  width: 3.1rem;
  height: 4.6rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-end;
  background-color: transparent;
  padding: 0;
}

@media screen and (min-width: 1024px) {
  .open-menu-button {
    display: none;
  }
}
